import './App.css';
import { motion, AnimateSharedLayout } from "framer-motion"
import Name from './components/name';
import Image from './components/image';
import Overview from './components/overview';
import Chart from './components/chart';
import Appchart from './components/appchart';
import Social from './components/social';
import { useEffect, useState } from "react"
import {v4 as uuid} from "uuid"

let isRun = false;

function App() {

  const [welcome, setWelcome] = useState(false)
  const [index, setIndex] = useState(0)

  useEffect(() => {
    customScroll();
  }, [welcome])

  const customScroll = () => {
    const main = document.querySelector("#main");
    //document.documentElement.style.setProperty('--fullvh', `${window.innerHeight}px`);
    //document.documentElement.style.setProperty('--fullvw', `${window.innerWidth}px`);
    const sections = document.querySelectorAll("section");
    let percent = 0;
    let customIndex = 0;
    
    window.addEventListener("wheel", (e) => {
      e.preventDefault();
      console.log(e)
      if (!isRun && welcome) {
        if (e.deltaY > 0 && percent > -(100 / sections.length) * (sections.length - 1)) {
          percent -= (100 / sections.length);
          setIndex(++customIndex)
        }
        else if (e.deltaY < 0 && percent < 0) {
          percent += (100 / sections.length);
          setIndex(--customIndex)
        }
        main.style.transform = `translate(${percent}%)`;
      }
    }, { passive: false })

    main.addEventListener('transitionrun', () => {
      isRun = true;
    });

    main.addEventListener('transitionstart', () => {
      isRun = true;
    });

    main.addEventListener('transitioncancel', () => {
      isRun = false;
    });

    main.addEventListener('transitionend', () => {
      isRun = false;
    });
/*
    let lastTouch = null

    window.addEventListener('touchstart', function (event) {
      lastTouch = event.touches[0]
    })

    window.addEventListener('touchend', function (event) {
      let currentTouch = event.changedTouches[0]

      if (lastTouch) {
        const scroll = currentTouch.clientX - lastTouch.clientX
        if (!isRun && welcome) {
          if (scroll < -1 && percent > -(100 / sections.length) * (sections.length - 1)) {
            percent -= (100 / sections.length);
            setIndex(++customIndex)
          }
          else if (scroll > 1 && percent < 0) {
            percent += (100 / sections.length);
            setIndex(--customIndex)
          }
          main.style.transform = `translate(${percent}%)`;
        }
      }

      lastTouch = null

    })*/

  }

  const onClick = (event) => {
    const index = [...event.target.parentElement.children].indexOf(event.target)
    const main = document.querySelector("#main");
    const sections = document.querySelectorAll("section");
    const percent = (-100 / sections.length) * index;
    main.style.transform = `translate(${percent}%)`;
    
    setIndex(index)
  }


  const spring = {
    type: "spring",
    stiffness: 500,
    damping: 30
  };

  return (
    <>
      <motion.div animate={{ opacity: [0, 1, 0], transitionEnd: { visibility: "hidden" } }} transition={{ duration: 4, times: [.2, .5, 1] }} onAnimationComplete={() => setWelcome(true)} className="absolute flex justify-center items-center w-screen h-webkit md:h-screen z-20">
        <h2 className={'text-white sourceFont'} style={{ fontSize: 'clamp(2rem, 12vw, 15rem)' }}>WELCOME</h2>
      </motion.div>
      <motion.div animate={{ opacity: [0, 0, 1] }} transition={{ duration: 8, times: [.2, .5, .9, 1] }}>
        <main id="main" className={'md:h-screen h-webkit h-weband inline-flex flex-row'}>
          <section className={'flex flex-col md:flex-row  md:p-0 h-hscreen min-h-webkit min-h-weband  md:h-screen w-wscreen '}>
            <div className='flex-1 flex items-center justify-center'><Image /></div>
            <div className='flex-1 flex items-center justify-center'><Name /></div>
          </section>
          <section className={'flex  h-hscreen min-h-webkit min-h-weband  md:h-screen w-wscreen'}>
            <Overview />
          </section>
          <section className={'flex  h-hscreen min-h-webkit min-h-weband  md:h-screen w-wscreen'}>
            <Chart />
          </section>
          <section className={'flex  h-hscreen min-h-webkit min-h-weband  md:h-screen w-wscreen'}>
            <Appchart />
          </section>
          <section className={'flex  h-hscreen min-h-webkit min-h-weband  md:h-screen w-wscreen'}>
            <Social />
          </section>
        </main>
        <AnimateSharedLayout>
          <div className="w-screen gap-5 justify-center absolute bottom-4 indicators flex">
            {[...Array(5).keys()].map(w => <motion.div key={uuid()} className='flex justify-center items-center w-5 h-5 border cursor-pointer border-white rounded-full' onClick={onClick}>
              {index === w && <motion.div
                key={uuid()}
                layoutId="outline"
                className="outline w-3 h-3 rounded-full "
                initial={false}
                animate={{ backgroundColor: "#ffffff" }}
                transition={spring}
              />}
            </motion.div>)}
          </div>
        </AnimateSharedLayout>
      </motion.div>

    </>
  );
}

export default App;
